import React from "react";
import intuit from "../assets/intuit.jpeg";
import tekion from "../assets/tekion.png";
import workiva from "../assets/workiva.jpeg";
import "./workexperience.css";

export function WorkExperience() {
  return (
    <>
      <div className="box">
        <h2> Work Experience </h2>
        <hr />

        <h3 className="we">
          <b>Front End Engineer</b>{" "}
        </h3>
        {/* <span> ( 2016 August - 2021 March )</span> */}
        <div>
          <img src={intuit} height="40" width="40" alt="Intuit" />
          <span style={{ verticalAlign: "top", marginLeft: "10px" }}>
            Intuit Inc
          </span>
        </div>
        <div style={{ margin: "0 0 0 2%" }}>
          <h4 className="wes">
            {" "}
            <b> UI Developer for QuickBooks Online Billing </b>{" "}
          </h4>

          <ul className="wed">
            <li>
              Configuring the billing plugins for different environments ( prod
              / dev ), setup best practices for repositories using development
              tools like prettier, es-lint, git hooks to prevent bad commits ,
              set up CI/CD pipeline in Jenkins to prevent merging bad PRs.{" "}
            </li>
            <li>
              Regularly updating documentation of our repositories, upgrading
              libraries, participate in design discussions, doing code reviews,
              controlling the branching strategy for weekly releases, deploying
              builds to production and monitoring post deployment.
            </li>
            <li>Integrating component libraries with storybooks.</li>
            <li>
              Interact with PMs, XDs and backend engineers for understanding the
              requirements, feature estimations, designing and developing pages
              for various subscription related flows ( subscription setup,
              upgrade, downgrade, switching between monthly and annual plans,
              cancel, resubscribe, add-on subscriptions etc).
            </li>
            <li>
              Follow a test driven development approach, ensuring proper unit
              test coverage, and e2e tests with Cypress ensuring zero manual
              testing effort.
            </li>
            <li>
              Ensure that the page is responsive, passes lighthouse tests for
              performance and accessibility and certified by security plugins.{" "}
            </li>
            <li>
              Take care of internationalization of contents in various locales.
            </li>
          </ul>
        </div>
        {/* Tekion*/}
        <hr />

        <h3 className="we">
          <b>Senior Front End Engineer</b>{" "}
        </h3>
        <div>
          <img src={tekion} height="40" width="40" alt="Intuit" />
          <span style={{ verticalAlign: "top", marginLeft: "10px" }}>
            Tekion Corp
          </span>
        </div>
        <div style={{ margin: "0 0 0 2%" }}>
          <h4 className="wes">
            {" "}
            <b> UI Developer for Automotive Retail Cloud (ARC) </b>{" "}
          </h4>
        </div>
        <ul className="wed">
          <li>
            Implemented a common library for form validation and submission with
            custom hooks and Context API
          </li>
          <li>
            Developed web pages for ARC parts , where dealers can create
            sales/purchase orders, invoices , view performance summary, export
            sales data to PDF etc{" "}
          </li>
          <li>
            Developed auto-search components, virtualized tables which render
            thousands of rows and supports bulk updates, and custom hooks on top
            of react query{" "}
          </li>
        </ul>

        <hr />
        <h3 className="we">
          <b>Front End Performance Engineer Consultant</b>{" "}
        </h3>
        <div>
          <img src={workiva} height="40" width="40" alt="Intuit" />
          <span style={{ verticalAlign: "top", marginLeft: "10px" }}>
            Workiva
          </span>
        </div>
        <div style={{ margin: "0 0 0 2%" }}>
          <h4 className="wes">
            {" "}
            <b> Performance Engineer for Wdesk </b>{" "}
          </h4>
        </div>
        <ul className="wed">
          <li>
            Worked on the product wdesk, a product similar to Google docs
            enabling users create and edit documents. Developed framework for
            studying user interactions and logging the events in Amplitude,
            which was required for various AI driven frameworks and marketing
            team.
          </li>
          <li>
            Created framework to capture browser interactions, metrices, web
            vitals, JS errors, session traces, real time memory monitoring and
            load times in New relic. Created custom New relic dashboards with
            nerdpacks, nerdlets and NRQL( New Relic Query Language) which were
            consumed by care team.
          </li>
          <li>
            Analysed performance issues and improving it with techniques like
            code splitting, lazy loading, caching, parallelization of network
            calls, preloading, pre-fetching etc. Brought down page loads from 10
            sec to less than 4 sec
          </li>
        </ul>
      </div>
    </>
  );
}
